@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css?family=Orbitron:600&display=swap);
*{box-sizing:border-box;padding:0;margin:0;font-family:'Roboto', sans-serif}.root{min-height:100vh;min-width:100vw}@media (min-width: 320px){.root{background:url(/life_events_calendar/static/media/background.80c3bc10.png) no-repeat;background-size:100% 100%}.form-container{display:flex;flex-direction:column;justify-content:center;align-items:center}.form-container .dataInput,.form-container .submitInput{padding:.5em}.form-container .dataInput .error,.form-container .submitInput .error{color:#ffff00}.form-container input{width:100%;border-radius:.5em;padding:.3em}.events{display:flex;flex-direction:column;justify-content:center;align-items:initial;padding:20px;font-size:.8em;color:#f5f5f5}.events .event{background-color:rgba(255,0,255,0.8);margin:1em;line-height:1.6em;padding:1em;text-align:center;border:1px solid rgba(255,255,255,0.9);border-radius:.5em;word-break:break-word}.events .timer{display:flex;flex-direction:row;justify-content:center;align-items:center;flex-wrap:wrap}.events .timer .timer-item{display:flex;flex-direction:column;justify-content:center;align-items:center;width:20vw;height:20vw;border:2px solid #fff;border-radius:50%;margin:.3em;font-size:3vw;font-family:'Orbitron', sans-serif;color:black}.events .timer .timer-item.pastEvent{font-size:4vw;border:none !important}.events .timer .timer-item span{font-family:'Orbitron', sans-serif}.events .deleteBtn{padding:.6em 1em;border-radius:.5em;box-shadow:1px 2px 2px 1px #000;background-color:whitesmoke}}@media (min-width: 768px){.events{display:flex;flex-direction:row;justify-content:center;align-items:initial;flex-wrap:wrap;font-size:1em}.events .event{display:flex;flex-direction:column;justify-content:center;align-items:center;flex:0 1 40%}.events .event .timer-item{width:9vw;height:9vw;font-size:1.4vw}.events .event .timer-item.pastEvent{font-size:2vw}}@media (min-width: 1200px){.form{display:flex;flex-direction:row;justify-content:center;align-items:center}.form .submitInput input{padding:.5em}.events .event{flex:25% 1}.events .event .timer{padding:.8em}.events .event .timer .timer-item{width:7vw;height:7vw;font-size:1.2vw}}

